import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProzNotificationsComponent } from './proz-notifications/proz-notifications.component';

const routes: Routes = [
  {
    path: 'notifications', component: ProzNotificationsComponent,



  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzNotificationsRoutingModule { }
